body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main {
   padding-top: 50px;
   background: #ECEFF1;
   height: 100%;
   display: flex;
   flex-flow: row wrap;
   padding: 20px;
   justify-content: space-around;
   align-content: flex-start;
   overflow: auto;
 }
 
.customnavbarnav{
   width: 100%; 
   text-align: center;
 }
 .customlist{
   width: 33.33333333333333%
 }
 
 .navbar {
   padding-top: 30px;
   padding-bottom:30px;
   color:rgb(180, 180, 211);
   font-size:24px;
 }
 
 a{
   color:white;
 
 }
 
body{
   background-color: rgb(58, 110, 206);
   color:rgb(95, 153, 19);
}

h1,h2{
   padding-top:20px;
   padding-bottom:20px;
}

.banner{
   width:100%;
}
.card-container {
   background: #fff;
   border-radius: 2px;
   height: 150px;
   margin: 1rem;
   position: relative;
   width: 150px;
   box-shadow: 0 3px 6px #999, 0 3px 6px #999;
   text-align: left;
 }

 .img-thumbnail{
   padding: 2px;
   margin:0 auto;
   height:150px;
   width: 100%;
   object-fit: cover;
 }

 


